import React from "react";
import { slideDown, slideUp } from "./anim";
import "./styles.css";

function formatDate(str) {
  return str.substr(0, 10);
}

function capitalize(str) {
  return str
    .split(" ")
    .map((s) => {
      return s.charAt(0).toUpperCase() + s.substr(1);
    })
    .join(" ");
}

class UserTableRow extends React.Component {
  state = { expanded: false };

  toggleExpander = (e) => {
    if (!this.state.expanded) {
      this.setState({ expanded: true }, () => {
        if (this.refs.expanderBody) {
          slideDown(this.refs.expanderBody);
        }
      });
    } else {
      slideUp(this.refs.expanderBody, {
        onComplete: () => {
          this.setState({ expanded: false });
        }
      });
    }
  };

  render() {
    const { user } = this.props;

    return [
      <tr key="main" onClick={this.toggleExpander}>
        <td>
          {capitalize(user.name.first + " " + user.name.last)}
          <br />
          <small>{user.email}</small>
        </td>
        <td>{formatDate(user.registered)}</td>
      </tr>,
      this.state.expanded && (
        <tr className="expandable" key="tr-expander">
          <td className="uk-background-muted" colSpan={2}>
            <div ref="expanderBody" className="inner uk-grid">
              <div>
                <table style={{ float: "left", width: "80%" }}>
                  <thead>
                    <tr>
                      <th>Date</th>
                      <th>Transaction</th>
                      <th>Balance</th>
                    </tr>
                  </thead>
                  <tbody>
                    {/* == TO DO == Map Transaction Array from API here */}

                    <tr>
                      <td>date</td>
                      <td>transaction</td>
                      <td>ammount</td>
                    </tr>

                    <tr>
                      <td>date</td>
                      <td>transaction</td>
                      <td>ammount</td>
                    </tr>
                  </tbody>
                </table>
                <div class="dashboardButtons">
                  <button class="dBbutton">Internal Transfer</button>
                  <button class="dBbutton">External Transfer</button>
                </div>
              </div>
            </div>
          </td>
        </tr>
      )
    ];
  }
}

export default class Admin extends React.Component {
  state = { users: null };

  // Sample API call

  componentDidMount() {
    fetch("https://randomuser.me/api/1.1/?results=3")
      .then((response) => response.json())
      .then((data) => {
        this.setState({ users: data.results });
      });
  }

  render() {
    const { users } = this.state;
    const isLoading = users === null;

    return (
      <main>
        <div class="adminButtons">
          <button class="dBbutton">Delete Acct.</button>
          <button class="dBbutton">Create Acct.</button>
          <button class="dBbutton">Edit Trans.</button>
        </div>

        <h1>Hello, Admin</h1>

        <div className="table-container">
          <div>
            <table>
              <thead>
                <tr>
                  <th>Account</th>
                  <th>Balance</th>
                </tr>
              </thead>
              <tbody>
                {isLoading ? (
                  <tr>
                    <td colSpan={2} className="uk-text-center">
                      <em className="uk-text-muted">Loading...</em>
                    </td>
                  </tr>
                ) : (
                  users.map((user, index) => (
                    <UserTableRow key={index} index={index + 1} user={user} />
                  ))
                )}
              </tbody>
            </table>
          </div>
        </div>
      </main>
    );
  }
}
